import React, { useReducer, useRef } from 'react'
import { BrowserRouter } from 'react-router-dom'
import Home from './containers/Home/Home'
import About from './containers/About/About'
import Projects from './containers/Projects/Projects'
import Contact from './containers/Contact/Contact'
import Skills from './containers/Skills/Skills'
import Nav from './components/Nav/Nav'
import Backdrop from './components/Backdrop/Backdrop'

const reducer = (state, action) => {
  switch(action.type){
    case 'showMenu': {
      return { showMenu: true }
    }
    case 'hideMenu': {
      return { showMenu: false }
    }
    default: {
      return state
    }
  }
}

function App() {

  const [state, dispatch] = useReducer(reducer, { showMenu: false })

  const homeRef = useRef()
  const aboutRef = useRef()
  const projectsRef = useRef()
  const skillsRef = useRef()
  const contactRef = useRef()

  const scrollHandler = (reference) => {
    reference.current.scrollIntoView({ behavior: 'smooth' })
  }

  const showMenuHandler = () => {
    dispatch({ type: 'showMenu'})
  }

  return (
    <BrowserRouter>
      <div className="App">
        {state.showMenu ? 
          <Backdrop 
            hideBackdrop={() => dispatch({ type: 'hideMenu'})} 
            onMenuClick={scrollHandler}
            homeRef={homeRef}
            aboutRef={aboutRef}
            projectsRef={projectsRef}
            skillsRef={skillsRef}
            contactRef={contactRef}
            /> 
            : null}
        <Nav 
          onMenuClick={showMenuHandler} 
          onContactClick={scrollHandler} 
          contactRef={contactRef}
          />
        <Home ref={homeRef}/>
        <About ref={aboutRef}/>
        <Projects ref={projectsRef}/>
        <Skills ref={skillsRef}/>
        <Contact ref={contactRef}/>
      </div>
    </BrowserRouter>
  );
}

export default App;
