import React from 'react'
import styles from './Backdrop.module.css'
import { Link } from 'react-router-dom'

export default function Backdrop({ hideBackdrop, onMenuClick, homeRef, aboutRef, projectsRef, skillsRef, contactRef }) {
  return (
    <div className={styles.Backdrop} onClick={hideBackdrop}>
      <div className={styles.InnerContainer}>
        <ul className={styles.Menu}>
          <li onClick={() => onMenuClick(homeRef)}>Home</li>
          <li onClick={() => onMenuClick(aboutRef)}>About</li>
          <li onClick={() => onMenuClick(projectsRef)}>Projects</li>
          <li onClick={() => onMenuClick(skillsRef)}>Skills</li>
          <li onClick={() => onMenuClick(contactRef)}>Contact</li>
        </ul>
      </div>
    </div>
  )
}
